___CSS_LOADER_EXPORT___.push([module.id, `/* Контейнер для выравнивания чекбокса и лейбла */
.checkbox-container {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  margin: 5px;
}

/* Внешний label как обертка */
.checkbox-label-wrapper {
  display: inline-flex;
  align-items: center;
  cursor: inherit; /* Наследуем курсор от контейнера */
}

/* Скрытие оригинального чекбокса */
.checkbox-input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  width: 0;
  height: 0;
}

/* Визуальное представление чекбокса (квадратик) */
.checkbox-visual {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 2px solid #adb5bd; /* Серая рамка */
  border-radius: 3px;
  margin-right: 8px; /* Отступ от текста */
  position: relative;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  flex-shrink: 0; /* Предотвращаем сжатие */
}

/* Иконки внутри (галочка и минус) */
.checkbox-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0); /* Скрыты по умолчанию */
  width: 14px; /* <--- Увеличиваем размер */
  height: 14px; /* <--- Увеличиваем размер */
  fill: white; /* Цвет иконки */
  transition: transform 0.15s ease-in-out;
}

/* Стили для состояния checked */
.checkbox-input:checked + .checkbox-visual {
  background-color: #007bff;
  border-color: #007bff;
}
.checkbox-input:checked + .checkbox-visual .checkbox-icon--checked {
  transform: translate(-50%, -50%) scale(1); /* Показываем галочку */
}

/* Стили для состояния indeterminate */
.checkbox-container--indeterminate .checkbox-input + .checkbox-visual {
  background-color: #007bff;
  border-color: #007bff;
}
/* Убеждаемся, что галочка скрыта в indeterminate состоянии */
.checkbox-container--indeterminate .checkbox-input:checked + .checkbox-visual .checkbox-icon--checked {
    transform: translate(-50%, -50%) scale(0);
}
.checkbox-container--indeterminate .checkbox-input + .checkbox-visual .checkbox-icon--indeterminate {
   transform: translate(-50%, -50%) scale(1); /* Показываем минус */
}


/* Стили для состояния disabled */
.checkbox-container--disabled {
  cursor: not-allowed;
  opacity: 0.6;
}
.checkbox-container--disabled .checkbox-label-wrapper {
    cursor: not-allowed;
}


/* Стиль для текстового лейбла */
.checkbox-text-label {
  font-size: 1rem;
  color: #333;
}

/* Состояние фокуса (для доступности) */
.checkbox-input:focus + .checkbox-visual {
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
  border-color: #80bdff;
} `, "",{"version":3,"sources":["webpack://./src/components/Checkbox/Checkbox.css"],"names":[],"mappings":"AAAA,iDAAiD;AACjD;EACE,oBAAoB;EACpB,mBAAmB;EACnB,eAAe;EACf,iBAAiB;EACjB,sBAAsB;EACtB,WAAW;AACb;;AAEA,8BAA8B;AAC9B;EACE,oBAAoB;EACpB,mBAAmB;EACnB,eAAe,EAAE,mCAAmC;AACtD;;AAEA,mCAAmC;AACnC;EACE,kBAAkB;EAClB,UAAU;EACV,oBAAoB;EACpB,QAAQ;EACR,SAAS;AACX;;AAEA,kDAAkD;AAClD;EACE,qBAAqB;EACrB,WAAW;EACX,YAAY;EACZ,yBAAyB,EAAE,gBAAgB;EAC3C,kBAAkB;EAClB,iBAAiB,EAAE,qBAAqB;EACxC,kBAAkB;EAClB,8EAA8E;EAC9E,cAAc,EAAE,yBAAyB;AAC3C;;AAEA,oCAAoC;AACpC;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,yCAAyC,EAAE,wBAAwB;EACnE,WAAW,EAAE,4BAA4B;EACzC,YAAY,EAAE,4BAA4B;EAC1C,WAAW,EAAE,gBAAgB;EAC7B,uCAAuC;AACzC;;AAEA,gCAAgC;AAChC;EACE,yBAAyB;EACzB,qBAAqB;AACvB;AACA;EACE,yCAAyC,EAAE,uBAAuB;AACpE;;AAEA,sCAAsC;AACtC;EACE,yBAAyB;EACzB,qBAAqB;AACvB;AACA,6DAA6D;AAC7D;IACI,yCAAyC;AAC7C;AACA;GACG,yCAAyC,EAAE,qBAAqB;AACnE;;;AAGA,iCAAiC;AACjC;EACE,mBAAmB;EACnB,YAAY;AACd;AACA;IACI,mBAAmB;AACvB;;;AAGA,gCAAgC;AAChC;EACE,eAAe;EACf,WAAW;AACb;;AAEA,uCAAuC;AACvC;EACE,6CAA6C;EAC7C,qBAAqB;AACvB","sourcesContent":["/* Контейнер для выравнивания чекбокса и лейбла */\n.checkbox-container {\n  display: inline-flex;\n  align-items: center;\n  cursor: pointer;\n  user-select: none;\n  vertical-align: middle;\n  margin: 5px;\n}\n\n/* Внешний label как обертка */\n.checkbox-label-wrapper {\n  display: inline-flex;\n  align-items: center;\n  cursor: inherit; /* Наследуем курсор от контейнера */\n}\n\n/* Скрытие оригинального чекбокса */\n.checkbox-input {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n  width: 0;\n  height: 0;\n}\n\n/* Визуальное представление чекбокса (квадратик) */\n.checkbox-visual {\n  display: inline-block;\n  width: 18px;\n  height: 18px;\n  border: 2px solid #adb5bd; /* Серая рамка */\n  border-radius: 3px;\n  margin-right: 8px; /* Отступ от текста */\n  position: relative;\n  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;\n  flex-shrink: 0; /* Предотвр